@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Roboto:wght@300&display=swap');

h2{
  text-decoration: underline;
  margin:20px;
  font-family: 'Raleway', sans-serif;
  font-weight: 900;
}

.first-container {
  height: 100%;
  display: flex;
  margin: 20px 20px;
  border-radius: 5px;
  flex-direction: column;
  border: 1px solid #c6c6c6;
}

.header-div {
  position: fixed;
  width: 100%;
  height: 40px;
  background-color: #326bf3;
  justify-content: center;
  display: flex;
  align-items: center;
}

.header-div h4 {
  color: white;
  font-family: 'Raleway', sans-serif;
  margin: 0;
  font-size: 16px;
}

h4{
  text-decoration: underline;
  margin:20px;
  font-family: 'Raleway', sans-serif;
  font-weight: 900;
}

h3 {
  margin: 10px;
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  font-weight: 900;
  color: rgb(0, 0, 0);
  margin-bottom: 10px;
  text-decoration: underline;
}

.list-container {
  margin: 0px 20px 10px;
  padding: 0
}

.list-container li p {
  text-align: justify;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  font-weight: 300;
  color: #000;
}

.main_div {
  margin-left: 10px;
}

.footer-div {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 38px;
  background-color: #326bf3;
  justify-content: center;
  display: flex;
  align-items: center;
}

.footer-div h4 {
  color: white;
  font-family: 'Raleway', sans-serif;
  margin: 0;
  font-size: 13px;
  font-weight: 900;
}

hr {
  border-top: 1px solid #cfcfcf;
}